import React, { Component } from 'react'

export default class Cart extends Component {
   
    
    render() {
        return (
            <div>
                <h3>{this.props.name} </h3> <h4>Quantity:{this.props.counter}</h4> <h5>Price:{this.props.price}$</h5>
                <img  style={{Width:'80px', height:'80px'}} src={this.props.pic} alt="pic"></img>

        <h3>total: {this.props.counter * this.props.price}</h3>
        <br></br>

            </div>
        )
    }
}
