import React, { Component } from 'react'

export default class Homepage extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
    }
    
    render() {
        return (
            <div>
                <h4>{this.props.name}</h4>
                <h4>Price:{this.props.price} $</h4>
                <img style={{Width:'80px', height:'80px'}} src={this.props.pic} alt="pic"></img>
                <button className="btn btn-warning" onClick={()=>{this.props.test(this.props.index)}}>Add to cart</button>

            </div>
        )
    }
}
