import React, { Component } from 'react'
import './App.css';
import Homepage from './components/Homepage.js'
import Cart from  './components/Cart.js'
import 'bootstrap/dist/css/bootstrap.min.css'
import armani from "./armani.jpg"
import boss from "./boss.jpg"
import rado from "./rado.jpg"
import rolex from "./rolex.jpg"



export default class App extends Component {

  state = { 
    list :[{name:'Rado' , price: 150 , counter:1,index:0 ,pic:rado},
    {name:'Rolex' , price: 550 , counter:1,index:1 , pic:rolex},
    {name:'Armani' , price: 200 , counter:1,index:2 , pic:armani},
  {name:'Boss' , price: 320 , counter:1,index:3 , pic:boss}
    ],
    flag: true,


    cart:[],
    Totalpaid:[],
    test:[],
    buy:[],



  
  }
  test =(num)=>{
    this.state.list.map((element,i)=>{
      if(element.index == num){
        this.setState({cart:[element,...this.state.cart]})
      }
    })
    this.setState({test:num})

  }
  Total =()=>{
    const m=this.state.cart.reduce((s,{index})=>(s[index]=(s[index]||0)+1,s),{});
    const t=Object.keys(m).map((key)=>({id:key , counter:m[key], total:m[key]*this.state.list[key].price, name:this.state.list[key].name, pic:this.state.list[key].pic}))
  var a=0;
  t.map((e)=>{
    a+=(this.state.list[e.id].price* e.counter);

  })
  return a

  
  }
  
  tocart=()=>{
    const m=this.state.cart.reduce((s,{index})=>(s[index]=(s[index]||0)+1,s),{});
    const t=Object.keys(m).map((key)=>({id:key , counter:m[key], total:m[key]*this.state.list[key].price, name:this.state.list[key].name, pic:this.state.list[key].pic}))
  var a=0;
  this.setState({cart:[],flag:false});
  this.setState({buy:t});
  this.state.buy.map((e)=>{
    a+=(this.state.list[e.id].price * e.counter);
    
  })
  this.setState({Totalpaid:a})

  


  };

  test3=()=>{
    const m=this.state.cart.reduce((s,{index})=>(s[index]=(s[index]||0)+1,s),{});
    const t=Object.keys(m).map((key)=>({id:key , counter:m[key], total:m[key]*this.state.list[key].price,price:this.state.list[key].price, name:this.state.list[key].name, pic:this.state.list[key].pic}))
 return(
   <div>
     {t.map((element)=>{
     return( <Cart counter={element.counter} name={element.name} price={element.price} index={element.index} pic={element.pic}  />)

   })}
   

   </div>
 )
    
  
  }

  

  show = () =>{
    if(this.state.flag === false){
      return( <div style={{textAlign:"center",position:"relative"}}>
        {this.test3()}
      <h2 style={{color:"red"}}>All Total:{this.Total()} $</h2>
      <br></br>
      <button className="btn btn-warning" onClick={()=>{this.tocart();(this.Total())}}>buy</button>
      </div>
      )
      
    
   
    }else{
      return(
        <div style={{textAlign:"center",position:"relative"}}>
          
          {this.state.list.map((element,index)=>{
            return  <Homepage name={element.name } price={element.price} index={index} test={this.test} pic={element.pic}  />


          })}
        </div>
      
      )
    }
    
  }
  render() {
    return (
      <div style={{textAlign:"center",position:"relative"}}>
        

       <button className="btn btn-primary" onClick={()=>{this.setState({flag:true})}}>Homepage</button>
       <button className="btn btn-primary" onClick={()=>{this.setState({flag:false})}}>Cart</button>
       {this.show()}
       

      </div>
    )
  }
}